import React, { ReactNode } from "react";
import * as bd from "../index";
import { Modal } from "react-bootstrap";
import { MsgBox } from ".";

let msgBoxList = [];

export const msgbox = (title, body, buttons) => {
    if (msgBoxList.length > 0) msgBoxList[0].show(title, body, buttons);
};

type MsgBoxContainerPropsType = {};

type MsgBoxContainerStateType = {
    list: any;
};

export class MsgBoxContainer extends React.Component<MsgBoxContainerPropsType, MsgBoxContainerStateType> {
    constructor(props) {
        super(props);
        this.state = { list: [] };
    }

    handleClose = (key: any) => {
        const item = this.state.list.find((item) => item.key === key);
        if (item) {
            setTimeout(() => {
                var new_list = this.state.list.filter((x) => x !== item);
                this.setState({ list: new_list });
            }, 1000);
        }
    };

    show = (title, body, buttons, props) => {
        const id = "m-" + Date.now();
        if (typeof buttons === "string") {
            var btn_title = buttons;
            buttons = (hide) => (
                <bd.Button variant="text" color="primary" onClick={hide}>
                    {btn_title}
                </bd.Button>
            );
        }
        if (Array.isArray(buttons)) {
            var a = buttons;
            buttons = (hide) =>
                a.map((x, xIndex) => (
                    <bd.Button key={xIndex} variant="text" color="primary" onClick={() => (!x.action ? hide() : x.action(hide))}>
                        {x.title}
                    </bd.Button>
                ));
        }
        const m = <MsgBox key={id} onClose={() => this.handleClose(id)} title={title} body={body} buttons={buttons} />;
        this.setState({ list: [...this.state.list, m] });
    };

    componentDidMount() {
        msgBoxList.push(this);
    }

    componentWillUnmount() {
        const index = msgBoxList.indexOf(this);
        if (index > -1) {
            msgBoxList.splice(index, 1);
        }
    }

    render() {
        return this.state.list.map((x) => x);
    }
}
