import classNames from "classnames";
import React, { ReactElement } from "react";
import SvgDragIndicator from "../icons/DragIndicator";
import SvgSubdirectoryArrowLeft from "../icons/SubdirectoryArrowLeft";
import SvgSubdirectoryArrowRight from "../icons/SubdirectoryArrowRight";
import * as bd from "../index";

interface ColumnProps {
    name: string;
    title: string;
    width?: any;
    cellCSS?: string | (({ row, rowIndex, columnName, columnIndex }) => string);
    headerCSS?: string | (({ columnName, columnIndex }) => string);
}

interface BasicTableProps {
    fullWidth?: boolean;
    hover?: boolean;
    striped?: boolean;
    compact?: boolean;
    nowrap?: boolean;
    fixed?: boolean;
    columns?: [];
    data?: [];
    selectedIndices?: [];
    selectedRows?: [];
    multiSelect?: boolean;
    singleSelect?: boolean;
    draggable?: boolean;
    disableToggleOnClick?: boolean;

    className?: string;
    style?: object;
    maxHeight: any;
    maxWidth: any;
    hideHeader?: boolean;
    actions?: React.ReactNode;

    children: React.ReactNode;

    cellCSS: ({ row, rowIndex, columnName, columnIndex }) => string;
    headerCSS: ({ columnName, columnIndex }) => string;
    renderCell: ({ row, columnName, value, rowIndex, columnIndex }) => any;
    renderTD: ({ row, columnName, rowIndex, columnIndex, defaultProps, renderCell }) => React.ReactNode;
    onSelectionChanged: (indices: any, currrentIndex: number) => void;
    onDragStart: (ev: any, fromRowIndex: number) => void;
    onDragEnd: (ev: any, fromRowIndex: number) => void;
    onCanDrop: (ev: any, fromRowIndex: number, newRowIndex: number, newColumnIndex: number) => boolean;
    onDrop: (ev: any, fromRowIndex: number, newRowIndex: number, newColumnIndex: number) => void;
    [x: string]: any;
}

export const BasicTable = ({
    hover,
    striped,
    compact,
    nowrap,
    fixed,
    columns,
    data,
    selectedIndices,
    multiSelect,
    singleSelect,
    draggable,
    disableToggleOnClick,
    className,
    style,
    maxHeight,
    maxWidth,
    children,
    actions,
    hideHeader,
    renderCell,
    renderTD,
    onSelectionChanged,
    onDragStart,
    onDragEnd,
    onCanDrop,
    onDrop,
    cellCSS,
    headerCSS,
    ...props
}: BasicTableProps) => {
    if (multiSelect) singleSelect = false;
    const dragStartRowIndex = React.useRef();
    const cn = classNames("bd-table", className, {
        "bd-table-full-width": !fixed,
        "bd-table-hover": hover,
        "bd-table-striped": striped,
        "bd-table-compact": compact,
        "bd-table-nowrap": nowrap,
        "bd-table-fixed": fixed,
        "cur-pointer": (singleSelect || multiSelect) && !disableToggleOnClick,
    });

    const isSelected = (idx: number) => selectedIndices && selectedIndices.some((x) => x === idx);

    const toggleSelection = (idx) => {
        if (!onSelectionChanged) return;
        let list = [];

        const is_selected = isSelected(idx);
        if (singleSelect) {
            list = is_selected ? [] : [idx];
        } else {
            list = is_selected ? selectedIndices.filter((x) => x !== idx) : [...selectedIndices, idx];
        }

        onSelectionChanged(list, idx);
    };

    const toggleAllSelection = () => {
        if (!onSelectionChanged) return;
        let list = [];
        if (selectedIndices.length < data.length)
            for (let i = 0; i < data.length; i++) {
                list.push(i);
            }
        onSelectionChanged(list, null);
    };

    const handleRowClick = (idx) => {
        if (disableToggleOnClick) return;
        if (multiSelect || singleSelect) toggleSelection(idx);
    };

    const handleCellClick = (idx) => {
        if (multiSelect || singleSelect) toggleSelection(idx);
    };

    const getHeaderCSS = ({ columnName, columnIndex }) => {
        return headerCSS instanceof Function ? headerCSS({ columnName, columnIndex }) : headerCSS;
    };

    const getCellCSS = (args) => {
        return cellCSS instanceof Function ? cellCSS(args) : cellCSS;
    };

    const getTHProps = (c: ColumnProps, columnIndex: number) => {
        let p = { style: { width: c.width, minWidth: c.width } };
        const args = { columnName: c.name, columnIndex };
        var css = getHeaderCSS(args);
        var css_column = c.headerCSS instanceof Function ? c.headerCSS(args) : c.headerCSS;
        return { ...p, className: classNames(css, css_column) };
    };

    const getTDProps = (row, rowIndex, c: ColumnProps, columnIndex: number) => {
        var args = { row, rowIndex, columnName: c.name, columnIndex };
        var css = getCellCSS(args);
        var css_column = c.cellCSS instanceof Function ? c.cellCSS(args) : c.cellCSS;
        let p = { className: classNames(css, css_column) };
        if (hideHeader) p["style"] = { width: c.width, minWidth: c.width };
        return p;
    };

    const onDragStartHandler = (ev, rowIndex) => {
        ev.dataTransfer.effectAllowed = "move";
        dragStartRowIndex.current = rowIndex;
        ev.dataTransfer.setDragImage(new Image(), 0, 0);
        onDragStart && onDragStart(ev, rowIndex);
    };

    const onDragEndHandler = (ev, rowIndex) => {
        onDragEnd && onDragEnd(ev, rowIndex);
    };

    const onDragOverHandler = (ev, newRowIndex, newColumnIndex) => {
        const idx = dragStartRowIndex.current;
        if (onCanDrop && onCanDrop(ev, idx, newRowIndex, newColumnIndex)) ev.preventDefault();
    };

    const onDropHandler = (ev, newRowIndex, newColumnIndex) => {
        const idx = dragStartRowIndex.current;
        onDrop && onDrop(ev, idx, newRowIndex, newColumnIndex);
        ev.preventDefault();
    };

    const renderCellHandler = (row, columnName, rowIndex, columnIndex) => {
        const value = row[columnName];
        if (!renderCell) return value;
        return renderCell({ row, columnName, rowIndex, columnIndex, value });
    };

    const renderTDHandler = (row, rowIndex, column, columnIndex) => {
        var defaultProps = {
            ...getTDProps(row, rowIndex, column, columnIndex),
            key: rowIndex + "." + columnIndex,
            onDragOver: (ev) => onDragOverHandler(ev, rowIndex, columnIndex),
            onDragEnter: (ev) => onDragOverHandler(ev, rowIndex, columnIndex),
            onDrop: (ev) => onDropHandler(ev, rowIndex, columnIndex),
        };
        var defaultCellRenderer = () => renderCellHandler(row, column.name, rowIndex, columnIndex);

        if (renderTD) {
            var elem = renderTD({ row, columnName: column.name, rowIndex, columnIndex, defaultProps, renderCell: defaultCellRenderer });
            return elem ?? <React.Fragment key={rowIndex + "." + columnIndex}></React.Fragment>;
        }
        return <td {...defaultProps}>{defaultCellRenderer()}</td>;
    };

    return (
        <>
            <div className={cn} style={{ ...style, maxHeight, maxWidth }} {...props}>
                {data && data.length > 0 && (
                    <table>
                        {!hideHeader && (
                            <thead>
                                <tr>
                                    {(draggable || multiSelect || singleSelect) && (
                                        <th className={classNames("selection-column", getHeaderCSS({ columnName: null, columnIndex: -1 }))}>
                                            {multiSelect && (
                                                <bd.Toggle
                                                    size="sm"
                                                    disableRipple
                                                    indeterminate={selectedIndices.length > 0 && selectedIndices.length < data.length}
                                                    model={selectedIndices.length === data.length}
                                                    setModel={toggleAllSelection}
                                                />
                                            )}
                                        </th>
                                    )}
                                    {columns.map((c: ColumnProps, cIndex) => (
                                        <th key={cIndex} {...getTHProps(c, cIndex)}>
                                            {c.title}
                                        </th>
                                    ))}
                                </tr>
                            </thead>
                        )}
                        <tbody>
                            {data.map((row: object, rowIndex: number) => (
                                <tr
                                    key={rowIndex}
                                    className={isSelected(rowIndex) ? "selected" : null}
                                    onClick={() => handleRowClick(rowIndex)}
                                >
                                    {(draggable || multiSelect || singleSelect) && (
                                        <td
                                            onDragOver={(ev) => onDragOverHandler(ev, rowIndex, -1)}
                                            onDragEnter={(ev) => onDragOverHandler(ev, rowIndex, -1)}
                                            onDrop={(ev) => onDropHandler(ev, rowIndex, -2)}
                                            className={classNames(
                                                "cur-pointer",
                                                getCellCSS({ row, rowIndex, columnName: null, columnIndex: -1, value: null })
                                            )}
                                            onClick={() => handleCellClick(rowIndex)}
                                        >
                                            {draggable && (
                                                <span
                                                    className="cur-pointer"
                                                    draggable={draggable}
                                                    onDragStart={(ev) => onDragStartHandler(ev, rowIndex)}
                                                    onDragEnd={(ev) => onDragEndHandler(ev, rowIndex)}
                                                >
                                                    <SvgDragIndicator className="text-secondary-text" />
                                                </span>
                                            )}
                                            {multiSelect && (
                                                <bd.Toggle
                                                    size="sm"
                                                    disableRipple
                                                    model={isSelected(rowIndex)}
                                                    setModel={() => toggleSelection(rowIndex)}
                                                />
                                            )}
                                            {singleSelect && (
                                                <bd.Toggle
                                                    size="sm"
                                                    disableRipple
                                                    model={isSelected(rowIndex)}
                                                    setModel={() => toggleSelection(rowIndex)}
                                                    radio
                                                />
                                            )}
                                        </td>
                                    )}

                                    {columns.map((c: ColumnProps, columnIndex) => renderTDHandler(row, rowIndex, c, columnIndex))}
                                </tr>
                            ))}
                        </tbody>
                    </table>
                )}
                {(!data || data.length == 0) && children}
            </div>
            {actions && (
                <bd.Flex align="center" className="bg-shade-5 p-s-4 py-2 border-bottom" gap={3}>
                    <SvgSubdirectoryArrowRight className="rtl-d-none text-secondary-text" />
                    <SvgSubdirectoryArrowLeft className="ltr-d-none text-secondary-text" />
                    {actions}
                </bd.Flex>
            )}
        </>
    );
};
