import React, { ReactNode } from "react";
import * as bd from "../index";
import { Modal } from "react-bootstrap";

type MsgBoxPropsType = {
    title: ReactNode;
    body: ReactNode;
    buttons: any;
    size?: "sm" | "lg" | "xl";
    onClose: () => void;
};

type MsgBoxStateType = {
    open: boolean;
};

export class MsgBox extends React.Component<MsgBoxPropsType, MsgBoxStateType> {
    constructor(props) {
        super(props);
        this.state = { open: true };
    }

    renderButtons = () => {
        if (!this.props.buttons) return null;
        if (typeof this.props.buttons === "function") {
            return this.props.buttons(this.onHideHandler);
        }
        return <>{this.props.buttons}</>;
    };

    onHideHandler = () => {
        this.setState({ open: false });
        this.props.onClose();
    };

    render() {
        return (
            <>
                <Modal show={this.state.open} onHide={this.onHideHandler} size={this.props.size}>
                    {this.props.title && (
                        <Modal.Header className="p-4 pb-0 mb-n2">
                            <Modal.Title className="h5">{this.props.title}</Modal.Title>
                        </Modal.Header>
                    )}
                    <Modal.Body className="p-4">
                        <div className="text-secondary-text">{this.props.body}</div>
                        {this.props.buttons && <div className="pt-4 mb-n2 text-end">{this.renderButtons()}</div>}
                    </Modal.Body>
                </Modal>
            </>
        );
    }
}
