import classNames from "classnames";
import React, { ReactNode, useState } from "react";
import { Collapse } from "react-bootstrap";
import { List } from "./list";
import SvgChevronRight from "../icons/ChevronRight";
import * as bd from "../index";
import SvgCheckBox from "../icons/CheckBox";
import SvgCheckBoxOutlineBlank from "../icons/CheckBoxOutlineBlank";
import SvgRadioButtonChecked from "../icons/RadioButtonChecked";
import SvgRadioButtonUnchecked from "../icons/RadioButtonUnchecked";

interface ListItemProps {
    variant?: "" | "text" | "button" | "menu";
    className?: string;
    alignTop?: boolean;
    icon?: ReactNode;
    checked?: "" | true | false;
    radio?: boolean;
    primary: ReactNode;
    secondary?: ReactNode;
    inset?: boolean;
    selected?: boolean;
    compact?: boolean;
    expanded?: boolean;
    disabled?: boolean;
    controls?: any;
    children?: any;
    onClick?: (e: any) => any;
    onTitleClick?: (e: any) => any;
    [x: string]: any;
}

export const ListItem = ({
    variant,
    className,
    alignTop,
    icon,
    checked,
    radio,
    primary,
    secondary,
    inset,
    selected,
    compact,
    expanded,
    disabled,
    controls,
    onClick,
    onTitleClick,
    children,
    ...props
}: ListItemProps) => {
    const [isExpanded, setIsExpanded] = useState(expanded);

    const cn = classNames("bd-list-item", className, {
        "bd-list-menu": variant === "menu",
        "bd-list-button": variant === "button",
        "bd-list-inset": inset,
        "bd-list-selected": selected,
        "bd-list-align-top": alignTop,
    });

    const clickHandler = (e) => {
        if (onClick) {
            if (onClick(e) === false) return false;
        }
        if (variant === "text") return null;
        if (disabled) return;
        setIsExpanded(!isExpanded);
    };

    if (checked === true) icon = radio ? <SvgRadioButtonChecked className="text-primary" /> : <SvgCheckBox className="text-primary" />;
    if (checked === false) icon = radio ? <SvgRadioButtonUnchecked /> : <SvgCheckBoxOutlineBlank />;

    if (children && children.length > 0) {
        if (children[0]?.type !== List) children = <List>{children}</List>;
    }

    return (
        <li className={cn} {...props}>
            <bd.Toolbar size={compact ? "sm" : "md"} onClick={clickHandler} className={classNames({ disabled })}>
                {icon && <div className={"bd-list-icon bd-list-py"}>{icon}</div>}

                <div className="bd-list-label" onClick={onTitleClick}>
                    <div>{primary}</div>
                    {secondary && <div className="text-secondary-text">{secondary}</div>}
                </div>

                {controls}

                {!!children && (
                    <span className="">
                        <SvgChevronRight
                            className={classNames("transition-transform", {
                                "rotate-90": isExpanded,
                                "rtl-rotate-180": !isExpanded,
                            })}
                        />
                    </span>
                )}
            </bd.Toolbar>

            {children && (
                <Collapse in={isExpanded}>
                    <div>{children}</div>
                </Collapse>
            )}
        </li>
    );
};
