import React, { MutableRefObject, useEffect, useRef, useState } from "react";
import createResizeDetector from "element-resize-detector";

interface WithOnResizeProps {
    as?: any;
    dimension?: "" | "width" | "height";
    children: React.ReactNode;
    onResize: (width: number, height: number) => void;
    [x: string]: any;
}

const erd = createResizeDetector("scroll");
const watchers = [];
function getWatcher(elem) {
    for (var i = 0; i < watchers.length; ++i) if (watchers[i].elem == elem) return watchers[i];
    var v = { elem, width: 0, height: 0 };
    watchers.push(v);
    return v;
}

export const ResizeWatch = React.forwardRef(({ as, dimension, onResize, children, ...props }: WithOnResizeProps, ref: MutableRefObject<any>) => {
    if (!ref) ref = useRef(null);

    const sizeWatcher = (element) => {
        var w = element.offsetWidth;
        var h = element.offsetHeight;
        var z = getWatcher(element);
        var w_changed = w !== z.width;
        var h_changed = h !== z.height;
        var changed = dimension === "width" ? w_changed : dimension === "height" ? h_changed : w_changed || h_changed;
        z.width = w;
        z.height = h;
        if (changed) onResize(w, h);
    };

    useEffect(() => {
        var instance = ref.current;
        if (!onResize || !instance) return;
        erd.listenTo(instance, sizeWatcher);
        return () => erd.uninstall(instance);
    }, [ref, ref.current]);

    props = { ...props, ref };
    if (!as) as = "div";

    return React.createElement(as, props, children);
});
